<template>
	<div class="selector">
		<div class="selector-wrap">
			<div class="selector-wrap-current" @click.stop="toggleDropdown" :class="$props.class" v-bind:class="{opened: isOpened && !isDisabled, disabled: isDisabled}">
				<div class="item">{{ getSelected.name }}</div>
				<div class="desc" v-if="getSelected.desc">{{ getSelected.desc }}</div>
			</div>
			<ul class="selector-wrap-values">
				<template v-for="group in getValues">
					<li class="group" v-if="group.group">{{ group.group }}</li>
					<li class="item" v-for="item in group.items" @click="setCurrentOption(item)">
						<div class="name">{{ item.name }}</div>
						<div class="price">{{ item.price }}</div>
						<div class="desc" v-if="item.desc">{{ item.desc }}</div>
					</li>
				</template>
			</ul>
		</div>
		<div class="selector-factor" v-if="factor" v-bind:class="{disabled: isDisabled}">
			<div class="options">
				<span class="add" data-tooltip="Удалить 1 диск">-</span>
				<input type="text" inputmode="numeric" name="count" class="count" value="3" :disabled="isDisabled">
				<span class="del" data-tooltip="Добавить 1 диск">+</span>
			</div>
			<div class="delete" data-tooltip="Удалить все диски">
				<i class="icon"></i>
			</div>
		</div>
	</div>
</template>

<script>
/*import {isDisabled} from "bootstrap/js/src/util";*/

export default {
	name: "Selector",
	props: [
		'class',
		'values',
		'factor',
		'disabled'
	],
	data() {
		return {
			isOpened: false,
			isDisabled: false,
			getSelected: typeof this.values[0].items == "object" ? this.values[0].items[0] : this.values[0],
			getValues: typeof this.values[0].items == "object" ? this.values : [{items: this.values}],
		}
	},
	methods: {
		setCurrentOption(option) {
			console.log(option);
		},
		toggleDropdown() {
			if (!this.isDisabled) {
				this.isOpened = !this.isOpened;
			}
		},
		documentClicked() {
			if (this.isOpened) {
				this.isOpened = false
			}
		},
	},
	created() {
		/*console.log(this.values)*/
		document.addEventListener('click', this.documentClicked);
	},
	destroyed() {
		document.removeEventListener('click', this.documentClicked);
	}
}
</script>
